.checkbox__field:checked + .checkbox__content:after {
  opacity: 1;
}

.checkbox__field:focus + .checkbox__content:before {
  outline: 1px solid var(--color-gray-100);
  outline-offset: 1px;
}

.checkbox__field:disabled + .checkbox__content {
  opacity: .4;
  pointer-events: none;
}

html, body {
  scroll-behavior: smooth;
  overscroll-behavior-y: none;
}

.page {
  height: var(--vh);
  font-family: var(--font-family-primary);
  font-weight: var(--fw-400);
  letter-spacing: 0;
  color: var(--color-dark-400);
  background-color: var(--color-white);
  font-optical-sizing: auto;
  font-size: clamp(16px, 1vw, 20px);
  font-style: normal;
  line-height: 1.4;
}

.page__body {
  min-width: 320px;
  min-height: var(--vh);
}

.page.background {
  background-color: var(--color-blue-400);
}

.page.open {
  overflow: hidden;
}

.container {
  padding: 0 var(--container-offset);
  width: 100%;
  max-width: var(--container-width);
  margin: 0 auto;
}

.site-container {
  min-height: var(--vh);
  grid-template-rows: 1fr auto;
  grid-template-columns: 100%;
  display: grid;
}

.main {
  padding-top: var(--header-height);
}

.overlay {
  position: relative;
}

.overlay:before {
  content: "";
  z-index: 1;
  background: var(--gradient);
  position: absolute;
  inset: 0;
}

.background {
  background-color: var(--color-blue-100);
}

.background--dark {
  background-color: var(--color-blue-400);
}

.logo {
  width: 100%;
  max-width: 120px;
  margin-right: 20px;
}

.logo img {
  width: 100%;
}

.hidden {
  display: none !important;
}

.sr-only {
  -webkit-clip-path: inset(50%) !important;
  clip-path: inset(50%) !important;
  width: 1px !important;
  height: 1px !important;
  white-space: nowrap !important;
  clip: rect(1px, 1px, 1px, 1px) !important;
  border: 0 !important;
  margin: -1px !important;
  padding: 0 !important;
  position: absolute !important;
  overflow: hidden !important;
}

.dis-scroll {
  width: 100%;
  height: 100dvh;
  height: calc(var(--vh, 1vh) * 100);
  overscroll-behavior: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

h1, h2, h3 {
  width: -moz-fit-content;
  width: fit-content;
  font-weight: var(--fw-700);
  color: var(--color-dark-400);
  line-height: 1.05;
  overflow: hidden;
}

h1 {
  font-size: clamp(2.25rem, 1.9951rem + 1.0458vi, 56px);
}

@media only screen and (max-width: 768px) {
  h1 {
    font-size: 34px;
  }
}

h2, .title {
  letter-spacing: -.015em;
  text-align: left;
  color: inherit;
  margin-bottom: 2rem;
  font-size: clamp(2rem, 1.7143rem + 1.4286vi, 3rem);
}

h3, .title--sec {
  font-size: 32px;
}

p {
  font-size: 20px;
  line-height: 1.4;
}

.header {
  z-index: 10;
  width: 100%;
  background-color: var(--color-white);
  margin: 0 auto;
  padding: 24px 0;
  transition-property: transform, padding;
  transition-duration: .15s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.header button.formsapp-popup-button, .header .btn {
  color: var(--color-white) !important;
  padding: 18px 23px !important;
}

.header button.formsapp-popup-button:after, .header .btn:after {
  filter: invert();
}

@media only screen and (max-width: 1180px) {
  .header button.formsapp-popup-button, .header .btn {
    padding: 18px 10px !important;
  }
}

.header .btn--user {
  color: var(--color-blue-400) !important;
}

.header.hide {
  transform: translateY(calc(-100% - 30px));
}

.nav {
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: relative;
}

.nav__inner {
  width: 100%;
  justify-content: flex-end;
  display: flex;
}

@media only screen and (max-width: 992px) {
  .header .nav__inner {
    z-index: -1;
    padding-top: calc(var(--header-height) * 1.5);
    width: 100%;
    height: calc(var(--vh));
    background-color: var(--color-white);
    flex-direction: column;
    justify-content: flex-start;
    transition-property: transform;
    transition-duration: .4s;
    position: fixed;
    top: 0;
    left: 0;
    overflow-y: auto;
    transform: translateX(150%);
  }
}

.nav__list {
  align-items: center;
  margin-left: 36px;
  margin-right: auto;
  display: flex;
}

@media only screen and (max-width: 1280px) {
  .header .nav__list {
    margin-right: 10px;
  }
}

@media only screen and (max-width: 992px) {
  .header .nav__list {
    z-index: -1;
    flex-direction: column;
  }
}

.nav__item:not(:last-of-type) {
  margin-right: 13px;
}

@media only screen and (max-width: 1280px) {
  .nav__item:not(:last-of-type) {
    margin-right: 10px;
  }
}

@media only screen and (max-width: 992px) {
  .header .nav__item {
    margin-bottom: 30px;
  }
}

.nav__link {
  letter-spacing: .035em;
  white-space: nowrap;
  text-transform: capitalize;
  color: var(--color-dark-450);
  background-color: #0000;
  padding: 10px;
  font-size: 16px;
}

.nav__link.true {
  border-bottom: 1px solid var(--color-dark-400);
}

.nav__btns {
  display: flex;
}

.nav__btns .request-btn {
  min-width: 251px;
}

@media only screen and (max-width: 1180px) {
  .nav__btns .request-btn {
    min-width: 220px;
  }
}

.nav__btns .btn--user {
  margin-right: 14px;
}

@media only screen and (max-width: 992px) {
  .nav__btns {
    margin: 0 auto;
  }
}

@media only screen and (max-width: 475px) {
  .nav__btns {
    flex-direction: column;
  }
}

@media only screen and (max-width: 992px) {
  .nav .nav__list {
    margin: 0 auto;
  }

  .nav .nav__item {
    margin-right: 0;
  }

  .nav--footer {
    flex-direction: column;
  }

  .nav--footer .logo, .nav--footer .nav__list {
    margin-bottom: 20px;
  }
}

@media only screen and (max-width: 992px) and (max-width: 576px) {
  .nav--footer .nav__list {
    flex-direction: column;
  }
}

@media only screen and (max-width: 992px) {
  .nav--footer .nav__inner {
    flex-direction: column;
    align-items: center;
  }
}

[class].btn {
  border-radius: calc(var(--radius-main)  - 4px);
  width: 100%;
  font-weight: var(--fw-500);
  text-align: center;
  color: var(--color-dark-450);
  background-color: var(--color-white);
  border: 1px solid #0000;
  justify-content: center;
  align-items: center;
  padding: 18px 10px;
  font-size: 16px;
  line-height: 1;
  transition-property: background-color, color, border-color;
  transition-duration: .2s;
  display: flex;
  position: relative;
}

[class].btn:hover, [class].btn:focus, [class].btn--reverse {
  border-color: var(--color-blue-400);
  color: var(--color-white);
  background-color: var(--color-blue-400);
}

[class].btn--reverse:hover, [class].btn--reverse:focus {
  border-color: var(--color-blue-400);
  background-color: var(--color-white);
  color: var(--color-blue-400) !important;
}

[class].btn--user {
  min-width: 115px;
  position: relative;
}

[class].btn--user:before {
  content: "";
  width: 20px;
  height: 20px;
  background-image: var(--user);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  display: block;
  position: absolute;
  top: 50%;
  left: 5px;
  translate: 0 -50%;
}

[class].btn--user:hover:before, [class].btn--user:focus:before {
  filter: invert();
}

@media only screen and (max-width: 475px) {
  [class].btn {
    width: max-content;
    min-width: auto;
    margin: 0 auto;
    padding-left: 40px !important;
  }
}

button.formsapp-popup-button {
  margin: 0 !important;
  padding: 18px 23px !important;
}

[class].request-btn {
  font-weight: var(--fw-600);
  justify-content: space-between;
  font-size: 15px;
  display: flex;
  position: relative;
  color: var(--color-blue-400) !important;
}

[class].request-btn:after {
  content: "";
  width: 10px;
  height: 18px;
  background-image: var(--arrow);
  background-position: 0 0;
  background-repeat: no-repeat;
  background-size: contain;
  transition-property: filter;
  transition-duration: .2s;
  position: relative;
  right: 0;
}

[class].request-btn:hover:after, [class].request-btn:focus:after {
  filter: invert(0);
}

.arrow.btn {
  justify-content: space-between;
  padding: 17px 24px;
  display: flex;
  position: relative;
}

.arrow.btn:after {
  content: "";
  width: 10px;
  height: 18px;
  background-image: var(--arrow);
  background-position: 0 0;
  background-repeat: no-repeat;
  background-size: contain;
  transition-property: filter;
  transition-duration: .2s;
  position: relative;
  right: 0;
}

.burger {
  --line-height: 2px;
  --buger-size: 20px;
  --move: calc(var(--buger-size) / 3);
  --move-decrement: calc(var(--move) * -1);
  --transition-time: .5s;
  --lines-color: var(--color-black);
  width: calc(var(--buger-size)  + 5px);
  height: var(--buger-size);
  transition: opacity var(--transition-time) ease;
  background-color: #0000;
  display: none;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
}

.burger span {
  z-index: -1;
  width: 100%;
  height: var(--line-height);
  background-color: var(--lines-color);
  transition: transform var(--transition-time) ease, background-color var(--transition-time) ease .3s;
  display: block;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.burger span:before, .burger span:after {
  content: "";
  width: 100%;
  height: var(--line-height);
  background-color: var(--lines-color);
  animation-duration: .4s;
  animation-timing-function: cubic-bezier(.645, .045, .355, 1);
  animation-fill-mode: forwards;
  position: absolute;
  left: 0;
}

.burger span:before {
  transform: translateY(var(--move-decrement)) rotate(0);
}

.burger span:after {
  transform: translateY(var(--move)) rotate(0);
}

.burger:hover {
  opacity: 1;
}

@media only screen and (max-width: 992px) {
  .burger {
    display: block;
  }
}

.closed.opened span {
  background-color: var(--lines-color);
  transition: background-color .2s;
}

.closed.opened span:before {
  animation-name: toplineburger;
}

.closed.opened span:after {
  animation-name: bottomlineburger;
}

.opened .nav__inner {
  transform: translateX(0);
}

.opened span {
  background-color: #0000;
  transition: background-color .2s;
}

.opened span:before, .opened span:after {
  animation-duration: .2s;
  animation-timing-function: cubic-bezier(.645, .045, .355, 1);
  animation-fill-mode: forwards;
}

.opened span:before {
  animation-name: toplinecross;
}

.opened span:after {
  animation-name: bottomlinecross;
}

@keyframes toplinecross {
  0% {
    transform: translateY(var(--move-decrement));
  }

  50% {
    transform: translateY(0);
  }

  100% {
    transform: rotate(45deg);
  }
}

@keyframes bottomlinecross {
  0% {
    transform: translateY(var(--move));
  }

  50% {
    transform: translateY(0);
  }

  100% {
    transform: translateY(0)rotate(-45deg);
  }
}

@keyframes toplineburger {
  0% {
    transform: rotate(45deg);
  }

  50% {
    transform: rotate(0);
  }

  100% {
    transform: translateY(var(--move-decrement)) rotate(0);
  }
}

@keyframes bottomlineburger {
  0% {
    transform: translateY(0)rotate(-45deg);
  }

  50% {
    transform: rotate(0);
  }

  100% {
    transform: translateY(var(--move)) rotate(0);
  }
}

.policy {
