.blog {
  padding: 4.375rem 0 5.25rem;
}

.blog__grid {
  grid-template-columns: repeat(4, 1fr);
  gap: 24px;
  margin-bottom: 1.75rem;
  display: grid;
}

.blog__inner {
  border-radius: var(--radius-main);
  background-position: 100%;
  background-repeat: no-repeat;
  margin-bottom: 2.1875rem;
  padding: 2.1875rem 4.1875rem 5.9375rem;
  position: relative;
  overflow: hidden;
}

.blog__inner:before {
  content: "";
  z-index: 1;
  -webkit-clip-path: polygon(0 0, 59% 0%, 52% 100%, 0% 100%);
  clip-path: polygon(0 0, 59% 0%, 52% 100%, 0% 100%);
  width: 100%;
  height: 100%;
  background-color: var(--color-blue-400);
  position: absolute;
  top: 0;
  left: 0;
}

.blog__content {
  z-index: 1;
  max-width: 500px;
  color: var(--color-white);
  padding: 2rem 0;
  position: relative;
}

.blog__content h2 {
  letter-spacing: 0;
  margin-bottom: 24px;
  font-size: 32px;
  line-height: 1.2;
}

.blog__content p {
  margin-bottom: 40px;
}

.blog h1 {
  text-transform: none;
  margin-bottom: 44px;
}

.blog h1 + p {
  margin-bottom: 3.125rem;
}

.blog .btn {
  max-width: 160px;
  border-color: #0000;
  font-size: 19px;
}

.blog .btn:hover, .blog .btn:focus {
  border-color: var(--color-white);
}

.blog .btn:hover:after, .blog .btn:focus:after {
  filter: invert();
}

@media only screen and (max-width: 768px) {
  .blog__grid {
    grid-template-columns: 1fr;
  }

  .blog__inner {
    padding-bottom: 50px;
    position: relative;
  }

